import React, {Component} from 'react';

class Header extends Component {
    render() {
        return (
            <header>
            <nav className="Nav_Top cf">

                <a className="logo_destiny"></a>
                <a className="logo_bungie"></a>
            </nav>
            </header>
        );
    }
}

export default Header;


