import React, {Component} from 'react';

class RemainingDLs extends Component {
    constructor() {
    super();
    this.state = { 
        Remaining:132
    };
    this.timer = 0;
  }

  componentDidMount() {
    this.startTimer();
  }

  startTimer = () => {
    if (this.timer === 0) {
      this.timer = setInterval(this.countDown, 1000);
    }
  }

  countDown = () => {
    // Remove one second, set state so a re-render happens.
    let remaining = this.state.Remaining - Math.floor((Math.random()*5) + 1);
    this.setState({
      Remaining:remaining,
    });
    
    // Check if we're at zero.
    if (remaining === 0) { 
      clearInterval(this.timer);
    }
  }
    render() {
        return (
            
            <h5>Remaining Keys: {this.state.Remaining}</h5>

        );
    }
}

export default RemainingDLs;


