import React, {Component} from 'react';
import '../assets/css/custom.css';
import '../assets/css/loader.css'
import Header from '../Components/Header.js';
import RemainingDLs from '../Components/RemainingDLs.js';
import PS4Logo from '../assets/images/ps4-logo.png';
import XboxLogo from '../assets/images/xbox-logo.png';
import PCLogo from '../assets/images/pc-logo.jpg';
import scriptLoader from 'react-async-script-loader';



class App extends Component {
  constructor() {
    super();
    this.state = {
      showResults: false,
      showLoading: false
    };
    this.timer = 0;
  }

  startTimer = () => {
    if (this.timer === 0) {
      this.timer = setInterval(this.countDown, 9000);
    }
  }

  countDown = () => {
    // Remove one second, set state so a re-render happens.
    clearInterval(this.timer);
    this.setState({
      showResults: true,
      showLoading: false
    });
  }
  hide = () => {
    this.setState({showResults: true});
  }
  loading = () => {
    this.setState({showLoading: true});
    this.startTimer();
  }
  openLocker = () => {
    window.call_locker();
  }
  render() {
    let randKey = Math.floor((Math.random() * 9 ) * 1132);
    return (
      <div>
        <Header/>
        <div className="container cf">
          <div className="leftPanel">
            <img
              className="leftPanel_img"
              src="https://www.bungie.net/img/theme/destiny/img/d2_code_redemption.jpg"></img>
          </div>
          <div className="rightPanel">
            <h1>
              <span>Destiny 2</span>
              <strong>Beta Key</strong>
              <span className="portal">Portal</span>
            </h1>
            <div className="rightPanel_Form">
              <ol>
                {this.state.showResults == false && this.state.showLoading == false && <li id="step_1" className="ready cf open ">
                  <p className="head ready">
                    <span>Select Your Platform</span>
                  </p>
                  <div className="panel platforms open">

                    <ul className="images">
                      <li>
                        <img onClick={this.loading} src={PS4Logo}></img>
                      </li>
                      <li>
                        <img onClick={this.loading} src={XboxLogo}></img>
                      </li>
                      <li>
                        <img onClick={this.loading} src={PCLogo}></img>
                      </li>
                    </ul>
                    <p className="head ready">
                      <h3>Limited Availability!</h3>
                    </p>
                  </div>
                </li>
}
                {this.state.showLoading == true &&
                
                <div>
                  <li id="step_load" className="ready cf open ">
                    <p className="head ready">
                      <div className="loader-icon"></div>
                      <RemainingDLs/>
                      <h4>Fetching your key</h4>
                    </p>
                  </li>
                </div>
}
                {this.state.showResults == true && 
                <li id="step_2" className="ready cf open">
                  <p className="head ready">
                  <span>Click Below To Get Your Key</span>
                  </p>
                  <p className="head ready style-1">
                  <input onClick={this.openLocker} type="text" value={"XXXX-XXXX-"+randKey}/>
                  <button className="button cta-color" onClick={this.openLocker} >GET KEY NOW</button>
                  
                  </p>
                  
                </li>
}
              </ol>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default scriptLoader(['https://www.areyouahuman.co/contentlockers/load.php?id=c926ef37ec47472268605b38d8521edc'])(App)



